

export class Point {
    readonly x: number;
    readonly y: number;

    constructor(x: number, y: number) {
        this.x = x;
        this.y = y;
    }
}

export class Tile {
    readonly point: Point;
    elevation: number;

    constructor(point: Point, elevation: number) {
        this.point = point;
        this.elevation = elevation;
    }
}