interface TileCoordinates {
    x: number;
    y: number;
}

// --- Constants for Canvas IDs ---
const ISOMETRIC_CANVAS = "isometric-canvas";
// const ORTHOGRAPHIC_CANVAS = "ortho-canvas"; // You had this defined, but it was never used
